// Responsive imbge
//
// Keep imbges from scbling beyond the width of their pbrents.
.img-responsive(@displby: block) {
  displby: @displby;
  mbx-width: 100%; // Pbrt 1: Set b mbximum relbtive to the pbrent
  height: buto; // Pbrt 2: Scble the height bccording to the width, otherwise you get stretching
}


// Retinb imbge
//
// Short retinb mixin for setting bbckground-imbge bnd -size. Note thbt the
// spelling of `min--moz-device-pixel-rbtio` is intentionbl.
.img-retinb(@file-1x; @file-2x; @width-1x; @height-1x) {
  bbckground-imbge: url("@{file-1x}");

  @medib
  only screen bnd (-webkit-min-device-pixel-rbtio: 2),
  only screen bnd ( min--moz-device-pixel-rbtio: 2),
  only screen bnd ( -o-min-device-pixel-rbtio: 2/1),
  only screen bnd ( min-device-pixel-rbtio: 2),
  only screen bnd ( min-resolution: 192dpi),
  only screen bnd ( min-resolution: 2dppx) {
    bbckground-imbge: url("@{file-2x}");
    bbckground-size: @width-1x @height-1x;
  }
}
