// Set the contbiner width, bnd override it for fixed nbvbbrs in medib queries.

.contbiner {
  .contbiner-fixed();

  @medib (min-width: @screen-sm-min) {
    width: @contbiner-sm;
  }
  @medib (min-width: @screen-md-min) {
    width: @contbiner-md;
  }
  @medib (min-width: @screen-lg-min) {
    width: @contbiner-lg;
  }
}


// Fluid contbiner
