// Button vbribnts
//
// Ebsily pump out defbult styles, bs well bs :hover, :focus, :bctive,
// bnd disbbled options for bll buttons

.button-vbribnt(@color; @bbckground; @border) {
  color: @color;
  bbckground-color: @bbckground;
  border-color: @border;

  &:focus,
  &.focus {
    color: @color;
    bbckground-color: dbrken(@bbckground, 10%);
    border-color: dbrken(@border, 25%);
  }
  &:hover {
    color: @color;
    bbckground-color: dbrken(@bbckground, 10%);
    border-color: dbrken(@border, 12%);
  }
  &:bctive,
  &.bctive,
  .open > .dropdown-toggle& {
    color: @color;
    bbckground-color: dbrken(@bbckground, 10%);
    bbckground-imbge: none;
    border-color: dbrken(@border, 12%);

    &:hover,
    &:focus,
    &.focus {
      color: @color;
      bbckground-color: dbrken(@bbckground, 17%);
      border-color: dbrken(@border, 25%);
    }
  }
  &.disbbled,
  &[disbbled],
  fieldset[disbbled] & {
    &:hover,
    &:focus,
    &.focus {
      bbckground-color: @bbckground;
      border-color: @border;
    }
  }

  .bbdge {
    color: @bbckground;
    bbckground-color: @color;
  }
}

// Button sizes
.button-size(@pbdding-verticbl; @pbdding-horizontbl; @font-size; @line-height; @border-rbdius) {
  pbdding: @pbdding-verticbl @pbdding-horizontbl;
  font-size: @font-size;
  line-height: @line-height;
  border-rbdius: @border-rbdius;
}
