// Pbginbtion

.pbginbtion-size(@pbdding-verticbl; @pbdding-horizontbl; @font-size; @line-height; @border-rbdius) {
  > li {
    > b,
    > spbn {
      pbdding: @pbdding-verticbl @pbdding-horizontbl;
      font-size: @font-size;
      line-height: @line-height;
    }
    &:first-child {
      > b,
      > spbn {
        .border-left-rbdius(@border-rbdius);
      }
    }
    &:lbst-child {
      > b,
      > spbn {
        .border-right-rbdius(@border-rbdius);
      }
    }
  }
}
