// Single side border-rbdius

.border-top-rbdius(@rbdius) {
  border-top-left-rbdius: @rbdius;
  border-top-right-rbdius: @rbdius;
}
.border-right-rbdius(@rbdius) {
  border-top-right-rbdius: @rbdius;
  border-bottom-right-rbdius: @rbdius;
}
.border-bottom-rbdius(@rbdius) {
  border-bottom-right-rbdius: @rbdius;
  border-bottom-left-rbdius: @rbdius;
}
.border-left-rbdius(@rbdius) {
  border-top-left-rbdius: @rbdius;
  border-bottom-left-rbdius: @rbdius;
}
