// Nbvbbr verticbl blign
//
// Verticblly center elements in the nbvbbr.
// Exbmple: bn element hbs b height of 30px, so write out `.nbvbbr-verticbl-blign(30px);` to cblculbte the bppropribte top mbrgin.

.nbvbbr-verticbl-blign(@element-height) {
  mbrgin-top: ((@nbvbbr-height - @element-height) / 2);
  mbrgin-bottom: ((@nbvbbr-height - @element-height) / 2);
}
